:root {
  --bg-color: #000014;
  --text-color: #fbfafa;
  --base-color: #282851;
  --base-color2: #5a559e;
  --base-color3: #9988f7;
  --someFonts-color: #e4c2ff;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

html, body {
  height: 100%;
  width: 100%;
  background: var(--bg-color);
  color: var(--text-color);
  font-family: Poppins;
  font-weight: 400;
  overflow: hidden;
}

a {
  color: var(--color-base);
  text-decoration: none;
}

li {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
}

.nav {
  width: 100%;
  height: 50px;
}

.nav_list {
  height: 100%;
  justify-content: flex-end;
  align-items: center;
  gap: 30px;
  margin: 0 20px;
  display: flex;
}

.nav_link {
  opacity: .7;
  box-shadow: 0 -15px 10px 0 var(--base-color2);
  border-radius: 10px;
  padding: 15px;
}

.nav_items:first-child {
  text-transform: uppercase;
  opacity: .8;
  margin: auto;
  font-weight: bold;
}

.hero_img-block {
  width: 450px;
  height: 450px;
  background-color: var(--color3);
  background: linear-gradient(to top, var(--base-color4) 15%, var(--color4) 50%, var(--base-color4) 85%);
  box-sizing: border-box;
  box-shadow: 0 -1px 58px var(--base-color3), 0 0 2px 1px var(--base-color3);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  animation: border 4s infinite;
  display: flex;
  position: relative;
  overflow: hidden;
}

.hero_block {
  max-width: 1400px;
  height: 500px;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.container_button {
  width: 200px;
  height: 400px;
  border-left: 0;
  border-right: 6px solid var(--base-color3);
  box-shadow: 2px 0 2px var(--base-color3), 4px 0 4px var(--base-color3), 6px 0 3px var(--base-color4);
  border-radius: 0% 100% 100% 0% / 33% 46% 54% 24%;
  position: relative;
}

.container_button button {
  cursor: pointer;
  background-color: inherit;
  width: 60px;
  height: 60px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  border: none;
  position: absolute;
}

.container_button button:hover {
  transform: scale(1.2);
}

